export default "@-webkit-keyframes spinrecord {\n  /* 0%   {background-color:red; left:0px; top:0px;} */\n  25% {\n    transform: rotate(90deg);\n  }\n  50% {\n    transform: rotate(180deg);\n  }\n  75% {\n    transform: rotate(270deg);\n  }\n  100% {\n    transform: rotate(360deg);\n  }\n}\n@keyframes spinrecord {\n  /* 0%   {background-color:red; left:0px; top:0px;} */\n  25% {\n    transform: rotate(90deg);\n  }\n  50% {\n    transform: rotate(180deg);\n  }\n  75% {\n    transform: rotate(270deg);\n  }\n  100% {\n    transform: rotate(360deg);\n  }\n}\n.recordplayer-container {\n  display: flex;\n  flex-flow: wrap;\n  justify-content: center;\n  padding-top: 56px;\n}\n.recordplayer-container .vinyl-record-title-container {\n  width: 50%;\n  background-color: #080d13;\n  padding: 20px 52px;\n  border-right: 0.25px solid grey;\n}\n.recordplayer-container .vinyl-record-title-container .current-record-title {\n  text-align: center;\n  color: #FFF;\n  font-size: 1.5rem;\n}\n.recordplayer-container .vinyl-record-title-container .current-record-subtitle {\n  text-align: center;\n  color: #FFF;\n  margin-bottom: 40px;\n}\n.recordplayer-container .vinyl-record-title-container .vinyl-record {\n  background-image: url('vinyl_record.png');\n  background-repeat: no-repeat;\n  width: 235px;\n  height: 235px;\n  background-size: 235px 235px;\n  margin: 0 auto 16px;\n}\n.recordplayer-container .vinyl-record-title-container .vinyl-record.spinning {\n  -webkit-animation: 2s linear infinite spinrecord;\n          animation: 2s linear infinite spinrecord;\n}\n.recordplayer-container .play-buttons {\n  width: 50%;\n  text-align: center;\n  background-color: #080d13;\n}\n.recordplayer-container .play-buttons .recordplayer-title {\n  background-color: #000;\n  margin: 0;\n  padding: 16px;\n  text-align: left;\n  color: #BD53C7;\n  text-transform: uppercase;\n  font-size: 1.2rem;\n  font-weight: normal;\n  border-bottom: 0.25px solid grey;\n}\n.recordplayer-container ol {\n  margin: 0;\n  text-align: left;\n  counter-reset: li;\n  /* Initiate a counter */\n  margin-left: 0;\n  /* Remove the default left margin */\n  padding-left: 0;\n  /* Remove the default left padding */\n}\n.recordplayer-container ol > li {\n  padding: 0;\n  position: relative;\n  /* Create a positioning context */\n  margin-bottom: 8px;\n  /*padding:4px 8px; */\n  /* Add some spacing around the content */\n  list-style: none;\n  /* Disable the normal item numbering */\n}\n.recordplayer-container ol > li:before {\n  content: counter(li);\n  /* Use the counter as content */\n  counter-increment: li;\n  /* Increment the counter by 1 */\n  /* Position and style the number */\n  position: absolute;\n  top: 2px;\n  left: 24px;\n  box-sizing: border-box;\n  /* Some space between the number and the content in browsers that support\n     generated content but not positioning it (Camino 2 is one example) */\n  margin-right: 8px;\n  padding: 4px;\n  color: #fff;\n  font-size: 1.8em;\n  text-align: center;\n}\n.recordplayer-container ol > li:last-child {\n  margin-bottom: 0;\n}\n.recordplayer-container ol > li .music-card {\n  background-color: #070b11;\n  color: white;\n  padding: 16px 32px;\n  min-width: 360px;\n  cursor: pointer;\n  border-bottom: 0.25px solid grey;\n}\n.recordplayer-container ol > li .music-card button, .recordplayer-container ol > li .music-card button.play {\n  display: none;\n}\n.recordplayer-container ol > li .music-card.active {\n  background-color: #222a3d;\n}\n.recordplayer-container ol > li .music-card.active button {\n  background-color: transparent;\n  color: white;\n  border: none;\n  background-image: url('pause.svg');\n  background-size: 16px 16px;\n  background-position: center;\n  width: 24px;\n  height: 24px;\n  background-repeat: no-repeat;\n  float: right;\n  margin-left: 16px;\n  margin-top: 4px;\n  display: block;\n}\n.recordplayer-container ol > li .music-card.active button.play {\n  background-image: url('play.svg');\n}\n.recordplayer-container ol > li .music-card.active button:focus {\n  outline: none;\n}\n.recordplayer-container ol > li .music-card.active .flex-row.track-progress {\n  display: flex;\n}\n.recordplayer-container ol > li .music-card > .flex-row:first-child {\n  padding-left: 24px;\n}\n.recordplayer-container ol > li .music-card > .flex-row.track-progress {\n  display: none;\n  margin-top: 16px;\n}\n.recordplayer-container ol > li .music-card > .flex-row.track-progress .total-track-duration {\n  background-color: black;\n  width: 100%;\n  height: 8px;\n  box-sizing: border-box;\n  border: 2px;\n  border-radius: 4px;\n}\n.recordplayer-container ol > li .music-card > .flex-row.track-progress .total-track-duration .percent-played {\n  background-color: #BD53C7;\n  width: 0%;\n  height: 4px;\n  border-radius: 4px;\n  border: 2px solid black;\n  box-sizing: content-box;\n}\n@media only screen and (max-width: 500px) {\n  .container-fluid.recordplayer-container {\n    padding: 0;\n  }\n  .container-fluid.recordplayer-container .vinyl-record-title-container {\n    width: 100%;\n  }\n  .container-fluid.recordplayer-container .vinyl-record-title-container .vinyl-record {\n    margin: 0 auto 16px;\n  }\n  .container-fluid.recordplayer-container .play-buttons {\n    width: 100%;\n    padding: 0 20px 24px;\n  }\n  .container-fluid.recordplayer-container .play-buttons ol > li .music-card {\n    padding: 16px 20px;\n  }\n  .container-fluid.recordplayer-container .play-buttons ol > li .music-card .total-track-duration {\n    height: 18px;\n    border: 8px;\n    border-radius: 9px;\n  }\n  .container-fluid.recordplayer-container .play-buttons ol > li .music-card .total-track-duration .percent-played {\n    height: 8px;\n    border: 5px solid black;\n    border-radius: 9px;\n  }\n  .container-fluid.recordplayer-container .play-buttons ol > li:before {\n    left: 16px;\n  }\n}\n@media only screen and (max-width: 360px) {\n  .recordplayer-container ol > li .music-card {\n    min-width: 0;\n  }\n}\n/*# sourceMapp